"use client";

import React from 'react';
import { TableCell } from "@/components/monday-table/cells";
import { useTableData } from "@/components/monday-table/hooks/use-table-data";
import { useSelection } from "@/components/monday-table/hooks/use-selection";
import type { Business } from "@/lib/types";
import { Button } from "@/components/ui/button";
import {
  Table,
  TableBody,
  TableCell as UITableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { MoreHorizontal, Search, Plus, ArrowUpDown } from "lucide-react";

const columns = [
  {
    id: "name",
    label: "Business Name",
    type: "text" as const,
    sortable: true,
  },
  {
    id: "owner",
    label: "Owner",
    type: "person" as const,
  },
  {
    id: "phone",
    label: "Phone",
    type: "text" as const,
  },
  {
    id: "type",
    label: "Business Type",
    type: "text" as const,
  },
  {
    id: "status",
    label: "Status",
    type: "status" as const,
    options: [
      { id: "active", label: "Active", color: "#00C875" },
      { id: "inactive", label: "Inactive", color: "#C4C4C4" },
      { id: "lead", label: "Lead", color: "#FDAB3D" },
    ],
  },
  {
    id: "notes",
    label: "Notes",
    type: "text" as const,
  },
];

export function BusinessTable({ data, onUpdate }: { data: Business[], onUpdate: (id: string, changes: Partial<Business>) => void }) {
  const {
    handleSort,
    handleFilter,
    sortConfig,
    filters,
  } = useTableData();

  const {
    selectedRows,
    toggleRowSelection,
    selectAllRows,
    isRowSelected,
  } = useSelection();

  return (
    <div className="w-full">
      <div className="flex items-center justify-between pb-4">
        <div className="flex w-full max-w-sm items-center space-x-2">
          <Search className="h-4 w-4 text-muted-foreground" />
          <Input
            placeholder="Search businesses..."
            className="h-9"
            value={filters["global"] || ""}
            onChange={(e) => handleFilter("global", e.target.value)}
          />
        </div>
        <Button size="sm" className="ml-auto">
          <Plus className="mr-2 h-4 w-4" />
          Add Business
        </Button>
      </div>

      <div className="rounded-md border">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead className="w-12">
                <input
                  type="checkbox"
                  checked={selectedRows.length === data.length}
                  onChange={() => selectAllRows(data.map(r => r.id))}
                  className="h-4 w-4"
                />
              </TableHead>
              {columns.map((column) => (
                <TableHead key={column.id}>
                  <div className="flex items-center space-x-2">
                    <span>{column.label}</span>
                    {column.sortable && (
                      <ArrowUpDown
                        className="h-4 w-4 cursor-pointer text-muted-foreground"
                        onClick={() => handleSort(column.id)}
                      />
                    )}
                  </div>
                </TableHead>
              ))}
              <TableHead className="w-12"></TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {data.map((row) => (
              <TableRow key={row.id}>
                <UITableCell className="w-12">
                  <input
                    type="checkbox"
                    checked={isRowSelected(row.id)}
                    onChange={() => toggleRowSelection(row.id)}
                    className="h-4 w-4"
                  />
                </UITableCell>
                {columns.map((column) => (
                  <UITableCell key={column.id}>
                    <TableCell
                      column={column}
                      value={row[column.id as keyof Business]}
                      onChange={(value) => onUpdate(row.id, { [column.id]: value })}
                    />
                  </UITableCell>
                ))}
                <UITableCell className="w-12">
                  <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                      <Button variant="ghost" className="h-8 w-8 p-0">
                        <MoreHorizontal className="h-4 w-4" />
                      </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="end">
                      <DropdownMenuLabel>Actions</DropdownMenuLabel>
                      <DropdownMenuItem>Edit</DropdownMenuItem>
                      <DropdownMenuItem className="text-destructive">Delete</DropdownMenuItem>
                    </DropdownMenuContent>
                  </DropdownMenu>
                </UITableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>

      <div className="flex items-center justify-between py-4">
        <p className="text-sm text-muted-foreground">
          {selectedRows.length} of {data.length} row(s) selected
        </p>
        <div className="flex space-x-2">
          <Button variant="outline" size="sm">Previous</Button>
          <Button variant="outline" size="sm">Next</Button>
        </div>
      </div>
    </div>
  );
}